import React, { Component } from 'react';
import { observable, decorate } from 'mobx';
import { observer } from 'mobx-react';
import { withStyles } from '@material-ui/core/styles';
import { ContentStyles } from '../styles.js';
import ReactMarkdown from 'markdown-to-jsx';
import {error } from '../content';
import { profile, education, experience, skills } from '../content/spa';
import clsx from 'clsx';
import { override } from './MarkDownComponents';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../static/css/markdown.css';

/*
  Content Main Class
*/
class Container extends Component {
  pageState = {};

  constructor(props) {
    super(props);
    this.pageState = {
      markdown: ''
    };
    this.loadMd();
  };

  loadMd = async () => {
    this.pageState.markdown = await (await this.fetchSelected()).text();
  };

  fetchSelected = async () => {
    let result = null;
    switch (this.props.selectedTab) {
      case 0:
        result = (await fetch(profile));
        break;
      case 1:
        result = (await fetch(experience));
        break;
      case 2:
        result = (await fetch(education));
        break;
      case 3:
        result = (await fetch(skills));
        break;
      default:
        result = (await fetch(error));
    }
    return result;
  };

  render() {
    const { classes, width } = this.props;
    const { markdown } = this.pageState;
    this.loadMd();

    return (
      <div className={clsx('Container', classes.container)} style={{ width: width }}>
        <div className={clsx('Content', classes.content)}>
          <ReactMarkdown options={override(classes)}>
            {markdown}
          </ReactMarkdown>
        </div>
      </div>
    );
  }
}

/*
  Define received props types for validation.
*/
Container.propTypes = {
};

/*
  MobX decorations.
*/
decorate(Container, {
  pageState: observable
});

export default withStyles(ContentStyles)(observer(Container));